import React from "react"

import { Link } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"

import "../styles/projects.css"

const projects = () => {
  return (
    <Layout>
      <SEO title="Projects" />
      <section id="projects">
        <div style={{ fontSize: 25, marginBottom: 20, color: "#c8d5c3" }}>
          Projects
        </div>
        <div>Here are some of the projects I made while working in ATS.</div>
        <div style={{ marginTop: 10 }}>
          <div className="contentContainer">
            <div className="project-container">
              <Link
                style={{ textDecoration: "none", fontWeight: "bold" }}
                to="/atsOperations"
              >
                Advance Telematics Solutions Operations System (Veda)
              </Link>
              <div style={{ color: "gray", fontSize: 15 }}>
                04/2019 - Current (Maintaining)
              </div>
              <div style={{ marginLeft: 10, marginBottom: 10 }}>
                A web based application that handles all of ATS's daily
                activities.
              </div>
              <Link
                style={{ textDecoration: "none", fontWeight: "bold" }}
                to="/atsInventory"
              >
                ATS Inventory System
              </Link>
              <div style={{ color: "gray", fontSize: 15 }}>
                04/2019 - Current (Maintaining)
              </div>
              <div style={{ marginLeft: 10, marginBottom: 10 }}>
                The goal of this system is to be able to monitor the current
                stock of devices and items the company has anytime.
              </div>
              <Link
                style={{ textDecoration: "none", fontWeight: "bold" }}
                to="/veda"
              >
                Veda
              </Link>
              <div style={{ color: "gray", fontSize: 15 }}>
                09/2020 - Present (Under Development)
              </div>
              <div style={{ marginLeft: 10, marginBottom: 10 }}>
                An upgraded version of ATS Operations System that is now faster
                with the use of NodeJs and ReactJs.
              </div>
              <div style={{ fontWeight: "bold" }}>Al Jurf Ticketing System</div>
              <div style={{ color: "gray", fontSize: 15 }}>
                05/2019 - 07/2019
              </div>
              <div style={{ marginLeft: 10, marginBottom: 10 }}>
                A web-based Ticketing system for Al Jurf Development Projects
                LLC that facilitates traffic fines made by the company's client.
              </div>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  )
}

export default projects
